.chatHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: grey;
    padding: 10px;
}
.chatHeader__hash {
    color: grey;
    font-size: 30px;
    padding: 10px;
}
.chatHeader__left > h3 {
    display: flex;
    align-items: center;
    color: white;
}
.chatHeader__right {
    display: flex;
    align-items: center;
    flex: 0.5px;
    justify-content:end;
    padding: 10px;
}
.chatHeader__right > .MuiSvgIcon-root{
    padding: 5px;
    cursor: pointer;
}
.chatHeader__right > .MuiSvgIcon-root:hover {
    color:  white;
}
.chatHeader__search {
    display: flex;
    align-items: center;
    color: grey;
    background-color: #2f3135;
    border-radius: 3px;
    padding: 3px;
}
.chatHeader__search > input {
    background: transparent;
    outline-width: 0;
    color: white;
    border: none;
}