.chat {
    display: flex;
    flex-direction: column;
    flex: 0.75px;
    background-color: #363a3f;
    height: 100vh;
}
.chat__message{
    flex: 1px;
}
.chat__input {
    color: lightgrey;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-radius: 5px;
    margin: 20px;
    border-top: 1px solid grey;
    background-color: #474b53;
}
.chat__input > form > input {
    padding: 15px;
    background: transparent;
    border: none;
    /* width: 100px; */
    outline-width: 0;
    color: white;
    font-size: large;
}
.chat__input > form {
    flex: 1;
}
.chatInput__button {
    display: none;
}
.chatInputIcon > .MuiSvgIcon-root {
    padding: 5px;
}